import React, { useState, useEffect } from "react";
import {
  Container,
  Typography,
  Button,
  Divider,
  IconButton,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Dialog,
  DialogTitle,
  DialogContent,
  TextField,
  Snackbar,
  SnackbarContent,
} from "@material-ui/core";
import { connect } from "react-redux";
import moment from "moment";
import { withRouter } from "react-router-dom";
import { ArrowBack, Close } from "@material-ui/icons";
import { red, green } from "@material-ui/core/colors";
import api from "../../api/api";
import baseurl from "../../api/baseurl";
import { logout } from "../../actions";

const Product = (props) => {
  const { logout, history, update, remove, product } = props;
  const [formvalues, setformvalues] = useState({
    id: "",
    code: "",
    name: "",
    price: "",
    fssai_id: "",
    image: "",
    description: "",
    mom: "",
    status: "",
    created: "",
    mom_name: "",
    phone: "",
    email: "",
    address: "",
  });

  const [wait, setwait] = useState(false);
  const [maindialog, setmaindialog] = useState(false);
  const [editdialog, seteditdialog] = useState(false);
  const [toast, settoast] = useState(false);
  const [message, setmessage] = useState(null);
  const [status, setstatus] = useState(null);

  useEffect(() => {
    document.title = "Products";
  }, []);

  const submitEdit = async (e) => {
    e.preventDefault();
    if (localStorage.getItem("mom-admin")) {
      try {
        setwait(true);
        await api.patch(`/api/product/${formvalues.id}`, formvalues, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("mom-admin"),
          },
        });
        update(formvalues);

        seteditdialog(false);
        setstatus(green[700]);
        setmessage("Product Updated Successfully");
        setwait(false);
        return settoast(true);
      } catch (err) {
        setwait(false);
        setstatus(red[700]);
        setmessage("Something went wrong");
        return settoast(true);
      }
    } else {
      return logout();
    }
  };

  const submitStatus = async () => {
    if (localStorage.getItem("mom-admin")) {
      try {
        await api.patch(
          `/api/product/status/${formvalues.id}`,
          {
            status: !formvalues.status,
          },
          {
            headers: {
              Authorization: "Bearer " + localStorage.getItem("mom-admin"),
            },
          }
        );
        update({ ...formvalues, status: !formvalues.status });
        setformvalues({ ...formvalues, status: !formvalues.status });
        setstatus(green[700]);
        setmessage("Product Status Updated Successfully");

        return settoast(true);
      } catch (err) {
        setstatus(red[700]);
        setmessage("Something went wrong");
        return settoast(true);
      }
    } else {
      return logout();
    }
  };

  const deletesubmit = async () => {
    if (localStorage.getItem("mom-admin")) {
      try {
        setwait(true);
        await api.delete(`/api/product/${formvalues.id}`, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("mom-admin"),
          },
        });
        remove(formvalues.id);
        setformvalues({
          id: "",
          code: "",
          name: "",
          price: "",
          fssai_id: "",
          image: "",
          description: "",
          mom: "",
          status: "",
          created: "",
          mom_name: "",
          phone: "",
          email: "",
          address: "",
        });
        setmaindialog(false);
        setstatus(green[700]);
        setmessage("Product Deleted Successfully");
        setwait(false);
        return settoast(true);
      } catch (err) {
        setwait(false);
        setstatus(red[700]);
        setmessage("Something went wrong");
        return settoast(true);
      }
    } else {
      return logout();
    }
  };

  const renderlist = () => {
    if (product.length) {
      return (
        <Table size="small">
          <TableHead>
            <TableRow>
              <TableCell align="center" padding="checkbox">
                #
              </TableCell>
              <TableCell>Name (MOM)</TableCell>
              <TableCell>Phone</TableCell>
              <TableCell>Product Name</TableCell>
              <TableCell>Price</TableCell>
              <TableCell>Approved</TableCell>
              <TableCell>Created date</TableCell>
              <TableCell>Featured Priority</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {product.map((a, index) => {
              return (
                <TableRow
                  hover
                  key={index}
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    setformvalues({ ...a });
                    setmaindialog(true);
                  }}
                >
                  <TableCell>
                    <Typography align="center">{index + 1}</Typography>
                  </TableCell>
                  <TableCell>
                    <Typography>{a.mom_name}</Typography>
                  </TableCell>
                  <TableCell>
                    <Typography>{a.phone}</Typography>
                  </TableCell>
                  <TableCell>
                    <Typography>{a.name}</Typography>
                  </TableCell>
                  <TableCell>
                    <Typography>{a.price}</Typography>
                  </TableCell>
                  <TableCell>
                    <Typography
                      style={{ color: a.status ? green[700] : red[700] }}
                    >
                      {a.status ? "Yes" : "No"}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography>
                      {moment(a.created).format("DD MMMM YYYY")}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography>{a.code}</Typography>
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      );
    } else {
      return (
        <Typography align="center" variant="h6" style={{ marginTop: 300 }}>
          NO DATA TO DISPLAY!
        </Typography>
      );
    }
  };

  const renderdialog = () => (
    <Dialog
      scroll="body"
      fullWidth
      maxWidth="xs"
      open={maindialog}
      onClose={() => {
        setformvalues({
          id: "",
          code: "",
          name: "",
          price: "",
          fssai_id: "",
          image: "",
          description: "",
          mom: "",
          status: "",
          created: "",
          mom_name: "",
          phone: "",
          email: "",
          address: "",
        });
        setmaindialog(false);
      }}
    >
      <div>
        <div
          style={{
            display: "flex",
            justifyContent: "flex-end",
            width: "100%",
            position: "absolute",
          }}
        >
          <IconButton
            onClick={() => {
              setformvalues({
                id: "",
                code: "",
                name: "",
                price: "",
                fssai_id: "",
                image: "",
                description: "",
                mom: "",
                status: "",
                created: "",
                mom_name: "",
                phone: "",
                email: "",
                address: "",
              });
              setmaindialog(false);
            }}
          >
            <Close />
          </IconButton>
        </div>
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            height: 350,
          }}
        >
          <img
            alt={formvalues.name}
            src={`${baseurl}${formvalues.image}`}
            style={{ objectFit: "cover", width: "100%" }}
          />
        </div>
        <div style={{ padding: 10 }}>
          <Typography style={{ fontSize: 11, fontWeight: 400 }}>
            NAME (MOM)
          </Typography>
          <Typography style={{ fontWeight: 500, marginBottom: 8 }}>
            {formvalues.mom_name}
          </Typography>
          <Typography style={{ fontSize: 11, fontWeight: 400 }}>
            PHONE
          </Typography>
          <Typography style={{ fontWeight: 500, marginBottom: 8 }}>
            {formvalues.phone}
          </Typography>
          <Typography style={{ fontSize: 11, fontWeight: 400 }}>
            EMAIL ADDRESS
          </Typography>
          <Typography style={{ fontWeight: 500, marginBottom: 8 }}>
            {formvalues.email}
          </Typography>
          <Typography style={{ fontSize: 11, fontWeight: 400 }}>
            ADDRESS
          </Typography>
          <Typography style={{ fontWeight: 500, marginBottom: 8 }}>
            {formvalues.address}
          </Typography>
          <Divider style={{ marginBottom: 10, marginTop: 10 }} />
          <Typography style={{ fontSize: 11, fontWeight: 400 }}>
            PRODUCT NAME
          </Typography>
          <Typography style={{ fontWeight: 500, marginBottom: 8 }}>{formvalues.name}</Typography>
          <Typography style={{ fontSize: 11, fontWeight: 400 }}>
            PRICE
          </Typography>
          <Typography style={{ fontWeight: 500, marginBottom: 8 }}>
            {formvalues.price}
          </Typography>
          <Typography style={{ fontSize: 11, fontWeight: 400 }}>
            FSSAI ID
          </Typography>
          <Typography style={{ fontWeight: 500, marginBottom: 8 }}>
            {formvalues.fssai_id}
          </Typography>
          <Typography style={{ fontSize: 11, fontWeight: 400 }}>
            PRODUCT DESCRIPTION
          </Typography>
          <Typography style={{ whiteSpace: "pre-line" }}>
            {formvalues.description}
          </Typography>
          <div
            style={{
              display: "flex",
              justifyContent: "flex-end",
              marginTop: 20,
            }}
          >
            <Button
              variant="contained"
              color={formvalues.status ? "secondary" : "primary"}
              onClick={submitStatus}
              style={{ marginRight: 10 }}
            >
              {formvalues.status ? "Disapprove" : "Approve"}
            </Button>
            <Button
              variant="contained"
              color="primary"
              style={{ marginRight: 10 }}
              onClick={() => {
                seteditdialog(true);
              }}
            >
              Edit
            </Button>
            <Button
              variant="contained"
              color="primary"
              style={{ marginRight: 10 }}
              onClick={() => {
                if (
                  window.confirm(
                    `Are you sure you want to delete this product?`
                  )
                ) {
                  return deletesubmit();
                }
              }}
            >
              Delete
            </Button>
            <Button
              variant="contained"
              color="primary"
              style={{ marginRight: 10 }}
              onClick={() => {
                setformvalues({
                  id: "",
                  code: "",
                  name: "",
                  price: "",
                  fssai_id: "",
                  image: "",
                  description: "",
                  mom: "",
                  status: "",
                  created: "",
                  mom_name: "",
                  phone: "",
                  email: "",
                  address: "",
                });
                setmaindialog(false);
              }}
            >
              Close
            </Button>
          </div>
        </div>
      </div>
    </Dialog>
  );

  const rendereditdialog = () => (
    <Dialog
      scroll="body"
      open={editdialog}
      onClose={() => {
        seteditdialog(false);
      }}
    >
      <DialogTitle>Edit Product</DialogTitle>
      <DialogContent dividers>
        <form onSubmit={submitEdit}>
          <TextField
            type="number"
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Featured Product Priority (if not necessary, leave it blank)"
            value={formvalues.code}
            onChange={(e) =>
              setformvalues({ ...formvalues, code: e.target.value })
            }
          />
          <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Product Name"
            value={formvalues.name}
            onChange={(e) =>
              setformvalues({ ...formvalues, name: e.target.value })
            }
          />
          <TextField
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Price"
            value={formvalues.price}
            onChange={(e) =>
              setformvalues({ ...formvalues, price: e.target.value })
            }
          />
          <TextField
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="FSSAI ID"
            value={formvalues.fssai_id}
            onChange={(e) =>
              setformvalues({ ...formvalues, fssai_id: e.target.value })
            }
          />
          <TextField
            required
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Product Description"
            multiline
            rows={5}
            value={formvalues.description}
            onChange={(e) =>
              setformvalues({ ...formvalues, description: e.target.value })
            }
          />

          <div
            style={{
              display: "flex",
              justifyContent: "flex-end",
              marginTop: 10,
            }}
          >
            <Button
              disabled={wait}
              type="submit"
              variant="contained"
              color="primary"
              style={{ marginRight: 10 }}
            >
              Update
            </Button>

            <Button
              variant="contained"
              onClick={() => {
                seteditdialog(false);
              }}
            >
              Cancel
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );

  return (
    <Container>
      <Typography variant="h6">
        <IconButton
          edge="start"
          onClick={() => {
            history.goBack();
          }}
        >
          <ArrowBack />
        </IconButton>
        Products Management
      </Typography>

      <Divider style={{ marginTop: 20 }} />
      {renderlist()}
      {renderdialog()}
      {rendereditdialog()}

      <Snackbar
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
        open={toast}
        autoHideDuration={3000}
        onClose={() => {
          settoast(false);
        }}
      >
        <SnackbarContent
          style={{ backgroundColor: status }}
          message={message}
          onClose={() => {
            settoast(false);
          }}
          action={
            <IconButton
              key="close"
              color="inherit"
              onClick={() => settoast(false)}
            >
              <Close />
            </IconButton>
          }
        ></SnackbarContent>
      </Snackbar>
    </Container>
  );
};
const mapstatetoprops = (state) => {
  return { product: state.product };
};
const mapdispatchtoprops = (dispatch) => ({
  update: (obj) => {
    dispatch({
      type: "UPDATE_PRODUCT",
      payload: obj,
    });
  },
  remove: (id) => {
    dispatch({
      type: "REMOVE_PRODUCT",
      payload: id,
    });
  },

  logout: () => dispatch(logout()),
});
export default connect(
  mapstatetoprops,
  mapdispatchtoprops
)(withRouter(Product));
